.backdrop {
  position: fixed;
  z-index: 200;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 184, 202, 0.7);
  opacity: 1;
  visibility: visible;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
}

.modal-window {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 280px;
  height: auto;
  overflow-y: auto;
  padding: 40px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background-color: $accent-bg-color;
  background-image: url(../images/aboutbg-mob@1x.png), linear-gradient(to top, #ffffff, #f8dbde);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/aboutbg-mob@2x.png);
    }
  color: $secondary-text-color;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 768px) {
    width: 450px;
    height: auto;
    background-color: $accent-bg-color;
    background-image: url(../images/aboutbg-desk@1x.png), linear-gradient(to top, #ffffff, #f8dbde);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/aboutbg-desk@2x.png);
    }
  }
  @media screen and (min-width: 1200px) {
    width: 560px;
    height: auto;
    background-color: $accent-bg-color;
    background-image: url(../images/aboutbg-desk@1x.png), linear-gradient(to top, #ffffff, #f8dbde);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/aboutbg-desk@2x.png);
    }
  }
}

.modal-window__btn--close {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  border: none;
  margin: 8px;
  padding-top: 5px;
  padding-bottom: 5px;
  cursor: pointer;
  background: transparent;
  fill: currentColor;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    .button-close__icon {
    fill: rgb(211, 106, 106);
    }
  }
}

.button-close__icon {
  fill: #ffa5ba;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-pre-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.58;
  color: $secondary-text-color;
  margin-bottom: 30px;
  @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.79;  
  }
  @media screen and (min-width: 1200px) {
      font-size: 16px;
      line-height: 1.75;
      margin-bottom: 35px;
  }
}

.modal-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.58;
  color: $main-text-color;
  margin-bottom: 17px;
  @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.79; 
  }
  @media screen and (min-width: 1200px) {
      line-height: 1.93; 
      margin-bottom: 35px;
  }
}